.fade-appear {
  opacity: 0;
}
.fade-appear-active {
  opacity: 1;
  transition: all 750ms;
}
.fade-enter {
  opacity: 0;
  right: -100%;
}
.fade-enter-active {
  opacity: 1;
  right: 0%;
  transition: all 750ms;
}
.fade-exit {
  opacity: 1;
  right: 0%;
}
.fade-exit-active {
  opacity: 0;
  right: 100%;
  transition: all 750ms;
}
