.header {
  top: 0px;
  background-color: #454545;
  font-weight: 500;
  font-size: 1.5em;
  color: white;
  position: sticky;
  justify-content: center;
  text-align: center;
  vertical-align: middle;
  line-height: 70px;
  width: 100%;
  height: 70px;
  border-width: 1px;
  border-style: solid;
  border-color: #454545;
  z-index: 30;
}
