.home {
  position: absolute;
  top: 70px;
  color: white;
  width: 100%;
  height: calc(100% - 70px);
  background-image: url("../Media/newback.png");
  background-size: cover;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: start;
}
.img {
  width: 300px;
  height: auto;
}

.title {
  margin: 0;
  font-size: 5em;
}
.line {
  display: flex;
  align-items: center;
  gap: 7px;
}
.container1 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-column: 1;
}
.container2 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-column: 2;
}
.footer {
  bottom: 0px;
  position: absolute;
  justify-self: center;
}
a {
  text-decoration: none;
  font-weight: 500;
  font-size: 1.5em;
  transition: 0.3s;
  color: white;
  &:hover {
    color: goldenrod;
    transition: 0.3s;
  }
}
.links {
  color: white;
  flex-direction: column;
  text-align: left;
}

.description {
  margin-top: 0px;
  position: relative;
  font-size: 1.4em;
}
